@import url(https://fonts.googleapis.com/css2?family=Viga&display=swap);
body {
  margin: 0;
  font-family: 'Viga', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --red: #f25655;
  --green: #228B22;
}

body{
  background-color: #ebebeb;
  color: #000;
}
main{
  padding-top: 3rem;
  max-width: 22rem;
  margin: 0 auto;
  text-align: center;
}

button{
  background-color: transparent;
  color: #f25655;
  border:0;
  display: inline-block;
  width: 6rem;
  cursor: pointer;
}

button.with-text{
  width: auto;
  background-color:rgba(197, 197, 197, 0.5);
  border-radius:10px;
  padding: 10px 20px;
  font-size: 2rem;
  line-height: 4rem;
}
button.with-text svg{
  width: 40px;
  float:left;
  margin: 5px;
}

label{
  display:block;
  margin-bottom: 10px;
  margin-top: 20px;
  text-transform: capitalize;
}
.slider{
    height: 40px;
    border: 2px solid var(--red);
    border-radius: 20px;
}

.thumb{
    background-color: var(--red);
    cursor:pointer;
    width:40px;
    height: 40px;
    border-radius: 20px;
    outline: none;
}

.slider.green{
    border: 2px solid var(--green);
}
.slider.green .thumb{
    background-color: var(--green);
}
label
{
    margin: 1rem auto;
    display: block;
}
